import { useState, useEffect, useRef } from "react";
import { useLocation, useNavigate, useOutletContext } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { RootState, AppDispatch } from "../../store";
import { fetchAds, setHasSeenAdToday } from "../../store/adsSlice";
import {
  fetchIndexPage,
  fetchVideoList,
  getActorsList,
  search,
} from "../../store/videoSlice";

import VideoHorizontalList from "../../components/Video/VideoHorizontalList";
import VideoMap from "../../components/Video/VideoMap";
import Carousels from "../../components/Carousels";
import Button from "../../components/Button";

import { CategoryData } from "../../type/categoryType";
import { fetchCategory } from "../../store/categorySlice";
import { fetchComicList } from "../../store/comicSlice";
import ComicMap from "../../components/Comic/ComicMap";
import Modal from "../../components/Modal";
import { SearchParam, Product } from "../../type/videoType";
import { IndexLayoutItem } from "../../type/initType";
import ComicHorizontalList from "../../components/Comic/ComicHorizontalList";
import { Product as ComicProduct } from "../../type/comicType";
import ActorMap from "../../components/Actor/ActorMap";
import ScrollToTopBtn from "../../components/ScrollToTopBtn";
import Loading from "../../components/Loading";
import AdBanner from "../../components/AdBanner";
import { setLanguage } from "../../store/initSlice";
import { getFontClass } from "../../utils/helper";
import EventModal from "../Event/EventModal";

interface Key2Param {
  guessYouLike: string;
  newHits: string;
  hotRecommendations: string;
}
interface Label2Param {
  熱門: string;
  最新: string;
  週排行: string;
  月排行: string;
}

function HomePage() {
  const videoMapRef = useRef<HTMLDivElement>(null);
  const { t, i18n } = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();
  const { state } = location;
  const categoryName = state?.categoryName || t("精選");

  const { setValue } = useOutletContext<{
    setValue: (newValue: number) => void;
  }>();
  const categoryRefs = useRef<(HTMLSpanElement | null)[]>([]);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [catConfig, setCatConfig] = useState<CategoryData | null>(null);

  const dispatch: AppDispatch = useDispatch();
  const adData = useSelector((state: RootState) => state.ads.adData);
  const init = useSelector((state: RootState) => state.init.init);
  const videoList = useSelector((state: RootState) => state.video.videoList);
  const comicList = useSelector(
    (state: RootState) => state.comic.comicListData
  );
  const category = useSelector((state: RootState) => state.category.category);
  const indexLayout = useSelector(
    (state: RootState) => state.video.indexLayoutList
  );
  const indexlayoutComic = useSelector(
    (state: RootState) => state.video.indexLayoutComicList
  );
  const actorList = useSelector((state: RootState) => state.video.actorList);
  const language = useSelector((state: RootState) => state.init.language);
  const hasSeenAdToday = useSelector((state: RootState) => state.ads.hasSeenAdToday);
  const hasError = useSelector((state: RootState) => state.error.hasError);

  const [openModal, setOpenModal] = useState(false);
  const [allVideo, setAllVideo] = useState<Product[]>([]);
  const [allComic, setAllComic] = useState<ComicProduct[]>([]);
  const [allActor, setAllActor] = useState<Product[]>([]);
  const [selectedOrder, setSelectedOrder] = useState<string>("latest");
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const orderList = [
    { key: "最新更新", value: "latest" },
    { key: "搶先發行", value: "released" },
    { key: "週排行", value: "weekhot" },
    { key: "月排行", value: "monthhot" }
  ];
  const [showEventModal, setShowEventModal] = useState(false);
  const now = new Date(); // 目前系統時間
  const startTime = new Date('2025-06-01T12:00:00+08:00'); // 台灣時間
  const endTime = new Date('2025-06-30T12:00:00+08:00');
  const isWithinTaiwanTimeRange = now >= startTime && now <= endTime;

  useEffect(() => { 
    if (!hasSeenAdToday && isWithinTaiwanTimeRange) {
      setShowEventModal(true);
      dispatch(setHasSeenAdToday(true));
    }
  }, []);

  useEffect(() => {
    console.log("indexLayout: ", indexLayout);
  }, [indexLayout]);
  const handleGetMoreVideo = () => {
    if (catConfig && videoList) {
      if (catConfig.type === "search") {
        const config: SearchParam = {
          url:
            catConfig.url.replace("?", "&") +
            `&order=${selectedOrder}` +
            `&num=${Number(videoList[catConfig.name_zh_CN].pageNum) + 1}`,
          name: catConfig.name_zh_CN,
          mode: "category",
        };
        dispatch(search(config));
      } else {
        dispatch(
          fetchVideoList({
            name: catConfig.name_zh_CN,
            url: `${catConfig.url}&order=${selectedOrder}`,
            type: catConfig.type,
            num: Number(videoList[catConfig.name_zh_CN].pageNum) + 1,
          })
        );
      }
    }
  };
  const handleGetMoreComic = () => {
    // console.log("更多漫畫");
    if (comicList) {
      dispatch(fetchComicList({ url: `num=${Number(comicList.pageNum) + 1}` }));
    }
  };

  const handleGetMoreActor = () => {
    // console.log("更多女優");
    if (actorList) {
      dispatch(getActorsList({ num: Number(actorList.pageNum) + 1 }));
    }
  };

  const handleInitAllVideo = () => {
    if (
      catConfig &&
      videoList &&
      videoList[catConfig.name_zh_CN] &&
      Number(videoList[catConfig.name_zh_CN].pageNum) === 1
    ) {
      setAllVideo([]);
      setAllVideo(videoList[catConfig.name_zh_CN].products);
    }
  };

  const handleInitAllComic = () => {
    if (catConfig && comicList && Number(comicList.pageNum) === 1) {
      setAllComic([]);
      setAllComic(comicList.products);
    }
  };

  const handleInitAllActor = () => {
    if (catConfig && actorList && Number(actorList.pageNum) === 1) {
      setAllActor([]);
      setAllActor(actorList.products);
    }
  };

  // Fetch initial data
  useEffect(() => {
    dispatch(fetchCategory());
    dispatch(fetchAds(63));
    setIsLoading(true);

    setTimeout(() => {
      setIsLoading(false);
    }, 1000);

    if (!indexLayout && !indexlayoutComic) {
      dispatch(fetchIndexPage());
    }
  }, [dispatch]);

  useEffect(() => {
    return () => {
      setIsLoading(true);
    }
  }, []);

  // Handle category selection
  useEffect(() => {

    setValue(0);
    const index =
      category?.findIndex(
        (obj) => obj.name === categoryName || obj.name_zh_CN === categoryName
      ) ?? 0;
    setSelectedIndex(index);
  }, [categoryName, state, setValue]);

  // Update category config and scroll into view
  useEffect(() => {
    if (category && !isLoading) {
      setCatConfig(category[selectedIndex]);
  
      requestAnimationFrame(() => {
        const targetElement =
          categoryRefs.current[
            selectedIndex === 0 ? selectedIndex + 1 : selectedIndex
          ];
        const container = document.querySelector(".hidden-scrollbar");
  
        if (targetElement && container) {
          const targetPosition =
            targetElement.offsetLeft -
            container.clientWidth / 2 +
            targetElement.clientWidth / 2;
  
          container.scrollTo({ left: targetPosition });
        }
      });
    }
  }, [selectedIndex, category, isLoading]);

  // Fetch video list for selected category
  useEffect(() => {
    setSelectedOrder("latest");
    if (videoMapRef.current) {
      videoMapRef.current.scrollTo({
        top: 0,
        behavior: "auto",
      });
    }
    if (catConfig) {
      if (catConfig.name === "遊戲" || catConfig.name === t("遊戲")) {
        navigate("/app/game");
      } else if ((catConfig.name === "活動" || catConfig.name === t("活動")) && isWithinTaiwanTimeRange) {
        navigate("/app/Event");
      } else if (catConfig.name === "情趣" || catConfig.name === t("情趣")) {
        navigate("/app/shop");
      } else if (catConfig.name === "漫畫" || catConfig.name === t("漫畫")) {
        setIsLoading(true);
        dispatch(fetchComicList(""))
          .then((result) => {
            setTimeout(() => {
              setIsLoading(false);
            }, 500);
          })
          .catch((error) => {});
      } else if (catConfig.name === "女優" || catConfig.name === t("女優")) {
        setIsLoading(true);
        dispatch(getActorsList({}))
          .then((result) => {
            setTimeout(() => {
              setIsLoading(false);
            }, 500);
          })
          .catch((error) => {});
      } else if (catConfig.type === "search") {
        // console.log("url: ", catConfig.url.replace("?", "&"));
        const config: SearchParam = {
          url: catConfig.url.replace("?", "&"),
          name: catConfig.name_zh_CN,
          mode: "category",
        };
        setIsLoading(true);
        dispatch(search(config))
          .then((result) => {
            setTimeout(() => {
              setIsLoading(false);
            }, 500);
          })
          .catch((error) => {});
      } else if (
        catConfig.name !== "精選"
      ) {
        setIsLoading(true);
        console.log(catConfig);
        dispatch(
          fetchVideoList({
            name: catConfig.name_zh_CN,
            url: `${catConfig.url}&order=latest`,
            type: catConfig.type,
          })
        )
          .then((result) => {
            setTimeout(() => {
              setIsLoading(false);
            }, 500);
          })
          .catch((error) => {});
      }
    }
  }, [catConfig, dispatch]);

  useEffect(() => {
    handleInitAllVideo();
    handleInitAllComic();
    handleInitAllActor();
  }, [videoList, comicList, actorList]);

  useEffect(() => {
    if (
      catConfig &&
      (catConfig.name === "漫畫" || catConfig.name === t("漫畫")) &&
      comicList &&
      Number(comicList.pageNum) !== 1
    ) {
      setAllComic((prevAllComic) => [...prevAllComic, ...comicList.products]);
    } else if (
      catConfig &&
      (catConfig.name === "女優" || catConfig.name === t("女優")) &&
      actorList &&
      Number(actorList.pageNum) !== 1
    ) {
      setAllActor((prevAllActor) => [...prevAllActor, ...actorList.products]);
    } else {
      if (
        catConfig &&
        videoList &&
        videoList[catConfig.name_zh_CN] &&
        Number(videoList[catConfig.name_zh_CN].pageNum) !== 1
      ) {
        setAllVideo((prevAllVideo) => [
          ...prevAllVideo,
          ...videoList[catConfig.name_zh_CN].products,
        ]);
      }
    }
  }, [videoList, comicList, actorList]);

  const handleOrder = (value: string) => {
    if (catConfig) {
      if (videoMapRef.current) {
        videoMapRef.current.scrollTo({
          top: 0,
          behavior: "auto",
        });
      }
      setSelectedOrder(value);
      if (catConfig.type === "search") {
        const config: SearchParam = {
          url: `${catConfig.url.replace("?", "&")}&order=${value}`,
          name: catConfig.name_zh_CN,
          mode: "category",
        };
        dispatch(search(config));
      } else {
        dispatch(
          fetchVideoList({
            name: catConfig.name_zh_CN,
            url: `${catConfig.url}&order=${value}`,
            type: catConfig.type,
          })
        );
      }
    }
  };

  useEffect(() => {
    console.log("category: ", category);
  }, [category])

  return (
    <div>
      {isLoading ? (
        <div>
          <Loading />
        </div>
      ) : (
        <div className="pb-16">
          <div className="hidden-scrollbar px-4 py-2 space-x-6 overflow-x-auto whitespace-nowrap">
            {category?.map((categoryData: any, index) => {
              if (
                (categoryData.name === "視訊" || categoryData.name === t("視訊")) ||
                ((categoryData.name === "活動" || categoryData.name === t("活動")) && !isWithinTaiwanTimeRange)
              ) {
                return (
                  index !== 0 && (
                    <a
                      href={categoryData.url}
                      target="_blank"
                      key={index}
                      ref={(el) => (categoryRefs.current[index] = el)}
                      onClick={() => setSelectedIndex(index)}
                      className={index === selectedIndex ? "text-red-500" : ""}
                      rel="noreferrer"
                    >
                      {categoryData[`name_${language}`]}
                    </a>
                  )
                );
              }
              return (
                index !== 0 && (
                  <span
                    key={index}
                    ref={(el) => (categoryRefs.current[index] = el)}
                    onClick={() => setSelectedIndex(index)}
                    className={
                      index === selectedIndex 
                        ? "text-red-500" 
                        : categoryData.name === "獨家"
                          ? "text-green-300"
                          : ""
                    }
                    style={categoryData.name === "獨家" ? {color: "rgb(2 255 0)"} : {}}
                  >
                    {categoryData[`name_${language}`]}
                  </span>
                )
              );
            })}
          </div>

          {showEventModal && <EventModal onClose={() => setShowEventModal(false)} open={true}/>}

          <Carousels
            data={adData["63"]?.data ?? []}
            imgHost={init?.img_host ?? ""}
          />
          {selectedIndex === 0 ? (
            <div className="py-4">
              {indexLayout?.map((item, index) => (
                <VideoHorizontalList
                  key={index}
                  title={item[`name_${language}`]}
                  cate={item.name}
                  data={item.products}
                  vWidth={(item.name === "歐美" || item.name === "欧美") ? "vertical" : item.cover}
                />
              ))}
              {indexlayoutComic?.map((item, index) => (
                <ComicHorizontalList
                  title={t("漫畫")}
                  data={item.products ?? null}
                  mode={"indexLayout"}
                  setSelectedIndex={setSelectedIndex}
                />
              ))}
            </div>
          ) : (
            <div className="flex flex-col py-4">
              {![
                "女優",
                "漫畫",
                "免費",
                t("女優"),
                t("漫畫"),
                t("免費"),
              ].includes(catConfig?.name ?? "") && (
                <div className="m-4 flex flex-wrap gap-y-2 gap-x-4 items-center justify-end">
                  <p className={getFontClass(language)}>{t("排序依")}</p>
                  <ul className="flex border border-red-500 rounded-md overflow-hidden">
                    {orderList.map((item, index) => (
                      <li
                        key={index}
                        className={`${getFontClass(language)} p-2 ${
                          item.value === selectedOrder ? "bg-red-500" : ""
                        }`}
                        onClick={() => handleOrder(item.value)}
                      >
                        {t(item.key)}
                      </li>
                    ))}
                  </ul>
                </div>
              )}
              {catConfig?.name &&
              videoList?.[catConfig.name_zh_CN]?.products ? (
                <VideoMap
                  ref={videoMapRef}
                  data={allVideo}
                  cate={catConfig?.name}
                  onReachBottom={handleGetMoreVideo}
                  lastPage={Number(videoList[catConfig.name_zh_CN].pageNum) === Number(videoList[catConfig.name_zh_CN].pageTotals)}
                />
              ) : (
                <>
                  {(catConfig?.name === "女優" ||
                    catConfig?.name === t("女優")) && (
                    <ActorMap
                      data={allActor}
                      onReachBottom={handleGetMoreActor}
                      lastPage={Number(actorList?.pageNum) === Number(actorList?.pageTotals)}
                    />
                  )}
                  {(catConfig?.name === "漫畫" ||
                    catConfig?.name === t("漫畫")) && (
                    <ComicMap
                      data={allComic}
                      onReachBottom={handleGetMoreComic}
                      lastPage={Number(comicList?.pageNum) === Number(comicList?.pageTotals)}
                    />
                  )}
                </>
              )}
            </div>
          )}

          <AdBanner styleProps={{bottom: "4rem"}}/>

          <Modal
            open={openModal}
            onClose={() => setOpenModal(false)}
            title={"更新版本"}
            content={[
              <div className="flex flex-col w-full items-start">
                <span>是否更新至1.2.13版本</span>
                <span>1.修正了播放器問題</span>
                <span>2.新增了xxx功能</span>
              </div>,
            ]}
            buttons={[
              <Button className="button-shorted active mt-2">馬上升級</Button>,
              <Button className="button-shorted inactive mt-2">
                暫不升級
              </Button>,
            ]}
          />
          <ScrollToTopBtn />
        </div>
      )}
    </div>
  );
}

export default HomePage;
