import React, { useEffect, useState } from "react";
import { Dialog, DialogActions, IconButton } from "@mui/material";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import { XMarkIcon } from "@heroicons/react/24/outline";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { RootState } from "../../../store";

interface EventModalProps {
  onClose: () => void;
  open: boolean;
  container?: HTMLElement | null;
}

const copyGiftCode = () => {
  const giftCode = "LOVEAPP";
  navigator.clipboard.writeText(giftCode).then(() => {
    console.log("Gift code copied to clipboard:", giftCode);
  });
};

const EventModal: React.FC<EventModalProps> = ({
  open,
  onClose,
  container,
}) => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const language = useSelector((state: RootState) => state.init.language);
  const [hadCopied, setHadCopied] = useState(false);

  return (
    <Dialog
      open={open}
      onClose={(event, reason) => {
        if (reason !== 'backdropClick' && reason !== 'escapeKeyDown') {
          onClose();
        }
      }}
      fullWidth
      container={container}
      PaperProps={{
        style: {
          maxHeight: "70vh",
          maxWidth: "100vw",
          overflowY: "visible",
          borderRadius: "16px",
          backgroundImage: "url(../../../images/event/bg.jpg)",
          backgroundSize: "cover",
          backgroundPosition: "center",
        },
      }}
    >
      <div className="relative rounded-xl p-4 w-full max-w-xl mx-auto">
        {/* 關閉按鈕 */}
        <button
          onClick={onClose}
          className="absolute text-2xl text-gray-600 bg-white rounded-full  border-2 border-black w-8 h-8"
          style={{
            lineHeight: "1rem",
            right: "-5%",
            top: "-10%",
            zIndex: "10",
          }}
        >
          ×
        </button>
        {/* 內容區塊 */}
        <div className="flex flex-col gap-4">
          {/* 圖片區塊 */}
          <div className="relative w-full" style={{ aspectRatio: "3/2" }}>
            <div
              className="absolute"
              style={{ top: "-30%", left: "-10%", width: "50%" }}
            >
              <img
                className="w-full"
                src="../../../images/event/dot-circle.png"
                alt="dec-circle"
              />
            </div>
            <div
              className="absolute"
              style={{ top: "20%", left: "-5%", width: "90%" }}
            >
              <img
                className="w-full"
                src="../../../images/event/ribbon.png"
                alt="ribbon"
              />
            </div>
            <div
              className="absolute  "
              style={{ bottom: "-10%", right: "-15%", width: "90%" }}
            >
              <img
                className="w-full "
                src="../../../images/event/phone.png"
                alt="phone"
              />
            </div>
            <div
              className="absolute"
              style={{ bottom: "0%", left: "0%", width: "50%" }}
            >
              <img
                className="w-full"
                src={`../../../images/event/modal-title-${language}.png`}
                alt="app-only"
              />
            </div>
          </div>

          {/* 文案區塊 */}
          <div className="flex flex-col items-center gap-2">
            <div className="flex items-center bg-gray-100 px-4 py-2 rounded w-full justify-between border-red-500 border-2">
              <p className="text-lg font-bold text-red-900 tracking-wide">
                LOVEAPP
              </p>
              <div className="flex w-full items-center justify-end">
                <span
                  className="text-red-900 cursor-pointer "
                  onClick={() => {
                    copyGiftCode();
                    setHadCopied(true);
                  }}
                >
                  <ContentCopyIcon fontSize="small" />
                </span>
                {hadCopied && (
                  <span className="text-red-900 text-xs"> {t("複製成功")} </span>
                )}
              </div>
            </div>

            <p className="text-center text-xl text-red-900 my-2">
              {t("收視期+折價劵免費兌換")}
            </p>

            <div className="flex flex-col gap-2 w-full">
              <button
                className="bg-red-500 text-white py-2 rounded"
                style={{ boxShadow: "inset 0 0px 18px rgb(234, 11, 11)" }}
                onClick={() => navigate("/app/Event")}
              >
                {t("前往活動專區")}
              </button>
            </div>
          </div>
        </div>
      </div>
    </Dialog>
  );
};

export default EventModal;
