import React from "react";
import { FixedSizeList, ListChildComponentProps } from "react-window";
import ComicCard from "../ComicCard";
import { ComicListData, Product } from "../../../type/comicType";
import { useSelector } from "react-redux";
import { RootState } from "../../../store";
import { useTranslation } from "react-i18next";
import { switchName } from "../../../utils/helper";

interface ComicHorizontalListProps {
  data: Product[] | null;
  title?: string;
  mode?: string;
  setSelectedIndex?: (index: number) => void;
}

const ComicHorizontalList: React.FC<ComicHorizontalListProps> = ({
  data,
  title,
  mode,
  setSelectedIndex,
}) => {
  const { t } = useTranslation();
  const category = useSelector((state: RootState) => state.category.category);
  const language = useSelector((state: RootState) => state.init.language);
  const categoryIndex =
    category?.findIndex((obj) => obj.name_zh_CN === t("漫畫")) ?? 0;
  if (!data || data.length === 0) {
    return null;
  }

  // Get screen width
  const screenWidth = window.innerWidth > 480 ? 480 : window.innerWidth;
  // Calculate column width
  const columnWidth = screenWidth * 0.45;
  const rowHeight = columnWidth * 1.491;

  const Column: React.FC<ListChildComponentProps> = ({ index, style }) => {
    const item = data[index];
    if (!item) {
      return null;
    }

    return (
      <div className="py-2 pl-4" style={{ ...style, height: "100%" }}>
        <ComicCard
          key={index}
          id={index.toString()}
          uuid={item.ci_uuid}
          title={switchName(item, language)}
          imageUrl={`https:${item.pic_small}`}
          mode={mode}
        />
      </div>
    );
  };

  return (
    <div className="hidden-scrollbar">
      <div className="flex justify-between items-end">
        {title && (
          <h2 className="text-xl font-bold pl-4 text-red-500">{title}</h2>
        )}
        <span
          className="text-sm pr-1 align-middle"
          style={{ lineHeight: "1" }}
          onClick={() =>
            setSelectedIndex ? setSelectedIndex(categoryIndex) : {}
          }
        >
          more
        </span>
      </div>
      <FixedSizeList
        className="List mb-4 px-2"
        height={rowHeight}
        itemCount={data.length}
        itemSize={columnWidth}
        layout="horizontal"
        width={screenWidth}
      >
        {Column}
      </FixedSizeList>
    </div>
  );
};

export default ComicHorizontalList;
