import React, { useEffect, useState } from "react";
import { FixedSizeList } from "react-window";
import VideoCard from "../VideoCard";
import { formatSecondsToHMS, switchName } from "../../../utils/helper";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../../store";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";

interface VideoHorizontalListParam {
  title?: string;
  loadMore?: boolean;
  isLinkEnabled?: boolean;
  toggleSelectItem?: (id: string) => void;
  selectItems?: string[];
  data: any;
  vWidth: string;
  cate: string;
}

const VideoHorizontalList: React.FC<VideoHorizontalListParam> = ({
  title,
  loadMore,
  isLinkEnabled,
  toggleSelectItem,
  selectItems,
  data,
  vWidth,
  cate,
}) => {
  // Move Hooks to the parent component
  const dispatch: AppDispatch = useDispatch();
  const navigate = useNavigate();
  const language = useSelector((state: RootState) => state.init.language);
  const init = useSelector((state: RootState) => state.init.init);
  const [imgPrefix, setImgPrefix] = useState("https://iqqtv.net/preview/80/b/");

  const { t } = useTranslation();

  // useEffect(() => {
  //   if (vWidth === "vertical") {
  //     setImgPrefix("https://tdtest84-ag.qcpro.vip/preview/50/s/");
  //   } else {
  //     setImgPrefix("https://tdtest84-ag.qcpro.vip/preview/80/b/");
  //   }
  // }, [vWidth]);

  if (!data || data.length === 0) {
    return null; // Render nothing if data is missing or empty
  }

  // Get screen width
  const screenWidth = window.innerWidth;
  // 每個格子的寬度
  var columnWidth = 0;
  var rowHeight = 0;

  if (vWidth === "vertical") {
    columnWidth = screenWidth * 0.45;
    rowHeight = columnWidth * 1.491;
  } else {
    columnWidth = screenWidth * 0.7;
    rowHeight = columnWidth / 1.3015;
  }

  // 獨家專區判斷設定
  const isExclusive = cate === "強檔獨家" || cate === "强档独家";
  const titleClass = isExclusive ? "text-white" : "text-red-500";
  const bgClass = isExclusive
    ? {
        background:
          "linear-gradient(360deg,rgb(1, 57, 1) 0%,rgb(0, 0, 0) 9%,rgb(0, 0, 0) 15%, rgb(32, 97, 8) 40%, rgb(11, 88, 3) 85%)",
        padding: "10px 0 0 0",
      }
    : {};

  interface ColumnProps {
    index: number;
    style: React.CSSProperties;
  }

  const Column: React.FC<ColumnProps> = ({ index, style }) => {
    useEffect(() => {
      console.log("VideoHorizontalList cate: ", cate);
    }, [cate]);
    const item = data[index];
    if (!item) {
      return null;
    }
    
    return (
      <div className="py-2 pl-4" style={{ ...style, height: "100%" }}>
        <VideoCard
          key={index}
          id={item.uuid}
          uuid={item.uuid}
          imageUrl={`${init?.img_host}/preview/80/b/${item.uuid}-800.jpg`}
          title={switchName(item, language)}
          preview={`https://tdtest84-ag.qcpro.vip/subpage/getMvPreview.php?uuid=${item.uuid}&v=1722229802`}
          duration={formatSecondsToHMS(Number(item.time))}
          vWidth={vWidth}
          cate={cate}
          mode={"horizon"}
        />
      </div>
    );
  };

  const handleGetMore = () => {
    const config = {
      cate: cate,
      title: title,
      mode: "indexLayout",
    };
    navigate("/app/section", { state: config });
  };

  return (
    <div className="hidden-scrollbar" style={bgClass}>
      <div className="flex justify-between items-end">
        {title && (
          <h2 className={`text-xl font-bold pl-4 ${titleClass}`}>{title}</h2>
        )}
        <span
          className="text-sm pr-1 align-middle"
          style={{ lineHeight: "1" }}
          onClick={handleGetMore}
        >
          more
        </span>
      </div>
      <FixedSizeList
        className="List mb-4 px-2"
        height={rowHeight}
        itemCount={data.length || 0}
        itemSize={columnWidth}
        layout="horizontal"
        width={screenWidth}
      >
        {Column}
      </FixedSizeList>
    </div>
  );
};

export default VideoHorizontalList;
