import React, { useEffect, useRef, useCallback, forwardRef } from "react";
import { useTranslation } from "react-i18next";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import ActorCard from "../ActorCard";
import { useSelector } from "react-redux";
import { RootState } from "../../../store";
import { switchName } from "../../../utils/helper";

interface ActorMapProps {
  title?: string;
  loadMore?: boolean;
  data: any[];
  onReachBottom?: () => void; // Function to load more data
  lastPage?: boolean;
}
const ActorMap = forwardRef<HTMLDivElement, ActorMapProps >(
  ({ title, loadMore, data, onReachBottom, lastPage = false }, ref) => {

  const { t } = useTranslation();
  const init = useSelector((state: RootState) => state.init.init);
  const observerRef = useRef<HTMLDivElement | null>(null); // 用来监视底部元素的 ref
  const language = useSelector((state: RootState) => state.init.language);
  
  useEffect(() => {
    if (!observerRef.current) {
      return;
    }

    const observer = new IntersectionObserver(
      (entries) => {
        const target = entries[0];
        console.log("Is intersecting:", target.isIntersecting);
        if (target.isIntersecting) {
          if (onReachBottom) {
            onReachBottom(); // 触发外部传入的函数
          }
        }
      },
      {
        root: null, // 设置监视范围为容器
        threshold: 0.5, // 目标部分可见即可触发
      }
    );
    observer.observe(observerRef.current);

    if (lastPage) {
      observer.unobserve(observerRef.current);
    }

    return () => {
      if (observerRef.current) {
        console.log("Observer detached from:", observerRef.current);
        observer.unobserve(observerRef.current);
      }
    };
  }, [onReachBottom]); // 仅监听 onReachBottom 变化
  
  useEffect(() => {
    console.log("data: ", data);
  }, [data])
  if (!data || data.length === 0) {
    return null;
  }

  return (
    <div
      id="actorMapContainer"
      className="actor-map-container"
      ref={ref}
    >
      <div className="flex px-4 items-end justify-between">
        {title && <h2 className="text-xl font-bold">{title}</h2>}
        {loadMore && (
          <span className="text-xs font-bold pl-4">
            {t("看更多")}
            <FontAwesomeIcon
              icon={faAngleRight}
              className="text-primary-active"
            />
          </span>
        )}
      </div>
      <div className="flex flex-wrap px-2 pb-2">
        {data.map((item: any, index: number) => (
          <div key={index} className="w-1/3 p-2">
            <ActorCard 
              id={index.toString()}
              url={item.search.replace('?', '&')}
              title={item.name}
              imageUrl={`${init?.img_host}${item.pic.replace(".", "") ?? ''}`}
            />
          </div>
        ))}
      </div>
      <div ref={observerRef} className="observer-target h-8">aaaadddd</div>
    </div>
  );
});
export default ActorMap;
