import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLongArrowUp } from "@fortawesome/free-solid-svg-icons";

const ScrollToTopButton = () => {
  const [isVisible, setIsVisible] = useState(false);
  // 控制按鈕顯示邏輯
  const toggleVisibility = () => {
    if (window.scrollY > 300) {
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  };
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  useEffect(() => {
    window.addEventListener("scroll", toggleVisibility);
    return () => window.removeEventListener("scroll", toggleVisibility);
  }, []);
  return (
    <>
      {isVisible && (
        <div
          onClick={scrollToTop}
          className={`fixed bottom-[150px] right-[20px] w-[50px] h-[50px] bg-background text-white rounded-full flex items-center justify-center cursor-pointer shadow-lg animate-bounce`}
        >
          <FontAwesomeIcon
            icon={faLongArrowUp}
            size="lg"
            className="text-gray-200"
          />
        </div>
      )}
    </>
  );
};

export default ScrollToTopButton;
